:root {
  --background-color: #e2e2e2;
  --text-color: #fff;
  --background-contrast: #131313;
  --accent-color: #ffab59;
  --button-text: #050505;
  --button-color--disable: #3da568;
  --button-color: #59ff75;
  --box-shadow: rgba(0, 0, 0, .267);
}

.darkmode {
  --background-color: #131313;
  --text-color: #000;
  --background-contrast: #fff;
  --accent-color: #d2fe59;
  --button-text: #050505;
  --button-color: #eca84e;
  --button-color--disable: #a0753c;
  --box-shadow: rgba(250, 150, 0, .09);
}

* {
  box-sizing: border-box;
  font-family: Krub, sans-serif;
  text-decoration: none;
  list-style: none;
}

html {
  transition: all .25s;
}

#Darkbutton {
  color: var(--background-contrast);
  cursor: pointer;
}

ul {
  padding: 0%;
}

i {
  padding-right: 10px;
}

a {
  color: var(--text-color);
  outline: none;
  text-decoration: none;
}

img {
  max-width: 100%;
  display: block;
}

body {
  width: 100vw;
  height: 100vh;
  background-color: var(--background-color);
  flex-direction: column;
  justify-content: center;
  align-items: center;
  gap: 20px;
  margin: 0 auto;
  padding: 20px;
  line-height: 1.5;
  display: flex;
}

.target {
  width: auto;
  min-width: 0%;
  height: auto;
  min-height: 10%;
  box-shadow: 0px 0px 19px 0px var(--box-shadow);
  background-color: var(--background-contrast);
  color: var(--text-color);
  border-radius: 1.8em;
  flex-direction: row;
  justify-content: center;
  align-items: flex-start;
  gap: 10px;
  margin: 0 auto;
  padding: 50px;
  transition: all .3s;
  display: flex;
}

form {
  width: 100%;
}

.after--timer {
  width: 100%;
  flex-direction: column;
  gap: 1em;
  display: flex;
}

.timer {
  flex-direction: column;
  display: flex;
}

.back * {
  color: var(--background-contrast);
}

.back {
  width: 100%;
  height: 10px;
  flex-direction: row;
  justify-content: center;
  align-items: center;
  gap: 10px;
  display: flex;
}

.link {
  color: var(--text-color);
  gap: 10px;
  display: flex;
}

iframe {
  border-radius: 1em;
}

.form {
  flex-direction: column;
  gap: 1em;
  display: flex;
}

.form label {
  width: auto;
  flex-direction: column;
  justify-content: space-between;
  gap: 2em;
  display: flex;
}

input, textarea {
  background-color: var(--backgorund-color);
  font: inherit;
  height: auto;
  width: auto;
  color: var(--button-text);
  border-radius: .5em;
  padding: 5px;
}

textarea {
  resize: none;
}

input {
  width: auto;
  border: 1px solid var(--background-color);
}

button {
  cursor: pointer;
  width: 100%;
  height: auto;
  color: var(--button-text);
  border: 1px solid var(--button-color);
  background-color: var(--button-color);
  box-shadow: 0px 0px 19px 0px var(--box-shadow);
  border-radius: .6em;
  margin: 0 auto;
  padding: 10px;
}

button:disabled {
  background-color: var(--button-color--disable);
}

.content {
  width: 100%;
  flex-direction: column;
  gap: 1em;
  display: flex;
}

@media screen and (min-width: 600px) {
  .target {
    min-width: 30%;
    flex-direction: row;
    display: flex;
  }

  form {
    width: 100%;
  }

  .form label {
    width: auto;
    flex-direction: row;
    justify-content: space-between;
    gap: 2em;
    display: flex;
  }

  .after--timer {
    width: 100%;
    flex-direction: column;
    gap: 1em;
    display: flex;
  }
}

